import Splide from '@splidejs/splide';

import type { CMSAppartmentItem, CMSImage } from '$types/api';

import { THUMBNAIL_KEYS } from './constants';

/**
 *
 * @param scope the scope to search in
 * @param selector the selector to search for
 * @param data the data to bind
 */
export const bindData = (scope: Element, selector: string, data: string | number) => {
  const elements = scope.querySelectorAll(selector);
  elements.forEach((element) => {
    element.innerHTML = String(data);
  });
};

/**
 * Gets the thumbnails associated with a given CMS appartment Item
 * @param apartment is the CMS Apartment Itme
 * @returns list of thumbnails
 */

export const getApartmentThumbnails = (apartment: CMSAppartmentItem): CMSImage[] => {
  const thumbnails: CMSImage[] = [];
  const keys = [...THUMBNAIL_KEYS];
  for (let i = 0; i <= keys.length; i++) {
    const thumbnail = apartment.fieldData[keys[i]];
    if (thumbnail) {
      thumbnails.push(thumbnail);
    }
  }
  return thumbnails;
};

/**
 * Sets up the Splide slider for the apartment.
 */
export const setupSplider = (element: HTMLElement) => {
  const splide = new Splide(element, {
    type: 'loop',
    pagination: false,
  });

  splide.mount();
};
