import { cloneNode } from '@finsweet/ts-utils';

import type { Pricing, Rental } from '$types/api';

import { SELECTORS } from './constants';

export class Tarifs {
  public tarifWrap: HTMLElement;
  public tarifBlockTemplate: HTMLElement;
  constructor() {
    this.tarifWrap = document.querySelector(SELECTORS.tarifWrap) as HTMLElement;
    this.tarifBlockTemplate = document.querySelector(SELECTORS.tarifBlock) as HTMLElement;
  }

  public populateTarifs(eachPricing: Pricing, currentApartment: Rental) {
    const tarifBlock = cloneNode(this.tarifBlockTemplate);
    const tarifName = tarifBlock.querySelector(SELECTORS.tarifName) as HTMLElement;
    const tarifNights = tarifBlock.querySelector(SELECTORS.tarifNights) as HTMLElement;
    const tarifTotalPrice = tarifBlock.querySelector(SELECTORS.tarifTotal) as HTMLElement;
    const tarifAvgPrice = tarifBlock.querySelector(SELECTORS.tarifAvg) as HTMLElement;
    const tarifRadio = tarifBlock.querySelector(SELECTORS.tarifRadio) as HTMLInputElement;
    const tarifDetails = tarifBlock.querySelector(SELECTORS.tarifDetails) as HTMLAnchorElement;

    tarifName.innerText = eachPricing.display_name;
    tarifNights.innerText = currentApartment.nb_nights.toString();
    tarifAvgPrice.innerText = eachPricing.avg_per_night.toString();
    tarifTotalPrice.innerText = eachPricing.total_amount.toString();
    tarifDetails.innerHTML = eachPricing.price_details;
    tarifRadio.value = eachPricing.short_name;
    tarifRadio.setAttribute('price', eachPricing.total_amount.toString());

    return tarifBlock;
  }
}
