export const displayElement = (element: HTMLElement, shouldShow: boolean) => {
  if (element) {
    if (shouldShow) {
      // If shouldShow is true, remove the data-hidden attribute to show the element
      element.removeAttribute('data-hidden');
    } else {
      // If shouldShow is false, set the data-hidden attribute to hide the element
      element.setAttribute('data-hidden', 'true');
    }
  }
};
