import type { Apartments } from './Apartmentssss';
import { SELECTORS } from './constants';
import { constructUrl } from './constructUrl';
import { convertDateFormat } from './convertDateFormat';
import { Counter } from './Counter';
import { displayElement } from './displayElement';
import { parseUrlParams } from './parseUrlParams';
import { reverseDateFormat } from './reverseDateFormat';
import type { Tarifs } from './Tarifs';

export class SingleApartmentSearchBar {
  public cityId: string;
  public checkInDate: string;
  public checkOutDate: string;
  public checkInDateElement: HTMLInputElement;
  public checkOutDateElement: HTMLInputElement;
  public totalCount: string;
  public adultCount: string;
  public infantCount: string;
  public babyCount: string;
  public totalCountElement: HTMLInputElement;
  public adultCountElement: HTMLElement;
  public infantCountElement: HTMLElement;
  public babyCountElement: HTMLElement;
  public adultCounterInstance: Counter;
  public infantCounterInstance: Counter;
  public babyCounterInstance: Counter;
  public queryUrl: string | undefined;
  public bookingSyncAptId: string;
  public apartmentAvailableBlock: HTMLElement;
  public apartmentNotAvailableBlock: HTMLElement;
  constructor() {
    this.cityId = (document.querySelector(SELECTORS.cityId) as HTMLElement).textContent as string;

    this.bookingSyncAptId = (document.querySelector(SELECTORS.bookingSyncId) as HTMLElement)
      .textContent as string;
    this.checkInDateElement = document.querySelector(SELECTORS.checkinInput) as HTMLInputElement;
    this.checkInDate = this.checkInDateElement.value;

    this.checkOutDateElement = document.querySelector(SELECTORS.checkoutInput) as HTMLInputElement;

    this.checkOutDate = this.checkOutDateElement.value;

    this.totalCountElement = document.querySelector(SELECTORS.totalCount) as HTMLInputElement;
    this.totalCountElement.readOnly = true;
    this.totalCount = this.totalCountElement.value as string;

    const metricChangeEvent = new Event('change');

    this.adultCountElement = document.querySelector(SELECTORS.adultCount) as HTMLElement;
    this.adultCount = this.adultCountElement.textContent as string;

    this.infantCountElement = document.querySelector(SELECTORS.infantCount) as HTMLElement;
    this.infantCount = this.infantCountElement.textContent as string;

    this.babyCountElement = document.querySelector(SELECTORS.babyCount) as HTMLElement;
    this.babyCount = this.babyCountElement.textContent as string;
    this.adultCounterInstance = new Counter(
      this.adultCountElement,
      SELECTORS.adultIncrement,
      SELECTORS.adultDecrement,
      2,
      this.totalCountElement,
      metricChangeEvent
    );
    this.infantCounterInstance = new Counter(
      this.infantCountElement,
      SELECTORS.infantIncrement,
      SELECTORS.infantDecrement,
      0,
      this.totalCountElement,
      metricChangeEvent
    );
    this.babyCounterInstance = new Counter(
      this.babyCountElement,
      SELECTORS.babyIncrement,
      SELECTORS.babyDecrement,
      0,
      this.totalCountElement,
      metricChangeEvent
    );
    this.apartmentAvailableBlock = document.querySelector(
      SELECTORS.apartmentAvailable
    ) as HTMLElement;
    this.apartmentNotAvailableBlock = document.querySelector(
      SELECTORS.apartmentNotAvailable
    ) as HTMLElement;
    const isThereSearchData = this.getSearchData();

    // updating all the inputs
    if (isThereSearchData) {
      this.updateSearchData();
    }
  }

  // for populating inputs with data from url if any
  public getSearchData() {
    const parsedUrlParams = parseUrlParams();

    if (!parsedUrlParams || !parsedUrlParams.checkinDate || !parsedUrlParams.checkoutDate) {
      return false;
    }

    this.checkInDate = parsedUrlParams.checkinDate;
    this.checkOutDate = parsedUrlParams.checkoutDate;
    this.totalCount = (
      parsedUrlParams.nbAdults +
      parsedUrlParams.nbChildren +
      parsedUrlParams.nbBabies
    ).toString();
    this.adultCount = parsedUrlParams.nbAdults.toString();
    this.infantCount = parsedUrlParams.nbChildren.toString();
    this.babyCount = parsedUrlParams.nbBabies.toString();
    return true;
  }

  public updateSearchData() {
    this.checkInDateElement.value = reverseDateFormat(this.checkInDate);
    this.checkOutDateElement.value = reverseDateFormat(this.checkOutDate);

    this.adultCountElement.textContent = this.adultCount;
    this.infantCountElement.textContent = this.infantCount;
    this.babyCountElement.textContent = this.babyCount;
    this.totalCountElement.value = this.totalCount;
    this.adultCounterInstance.setCount(Number(this.adultCount));
    this.infantCounterInstance.setCount(Number(this.infantCount));
    this.babyCounterInstance.setCount(Number(this.babyCount));
    const searchParams = {
      checkin_date: convertDateFormat(this.checkInDate),
      checkout_date: convertDateFormat(this.checkOutDate),
      nb_adults: this.adultCount,
      nb_children: this.infantCount,
      nb_babies: this.babyCount,
      city_id: this.cityId,
      bookingSyncAptId: this.bookingSyncAptId,
    };
    this.queryUrl = constructUrl(searchParams);
  }

  public reCollectSearchData = () => {
    this.checkInDate = this.checkInDateElement.value;
    this.checkOutDate = this.checkOutDateElement.value;
    this.totalCount = this.totalCountElement.value as string;
    this.adultCount = this.adultCounterInstance.getCount().toString();
    this.infantCount = this.infantCounterInstance.getCount().toString();
    this.babyCount = this.babyCounterInstance.getCount().toString();

    if (!this.checkInDate || !this.checkOutDate) {
      return;
    }
    const searchParams = {
      checkin_date: convertDateFormat(this.checkInDate),
      checkout_date: convertDateFormat(this.checkOutDate),
      nb_adults: this.adultCount,
      nb_children: this.infantCount,
      nb_babies: this.babyCount,
      city_id: this.cityId,
      bookingsync_apt_id: this.bookingSyncAptId,
    };

    this.queryUrl = constructUrl(searchParams);
    return this.queryUrl;
  };

  public async handleSearch(apartmentsInsatnce: Apartments, tarifsInstance: Tarifs) {
    // Collect query data
    this.reCollectSearchData();

    // Trigger search for apartment from collected query data
    const apartmentData = await apartmentsInsatnce.getApartmentsData(this.queryUrl);

    if (!apartmentData || apartmentData.errors) {
      // hide apartment available block
      displayElement(this.apartmentAvailableBlock, false);
      if (this.queryUrl) {
        displayElement(this.apartmentNotAvailableBlock, true);
      }

      return;
    }
    history.pushState(null, '', this.queryUrl);
    // resseting display of elements
    displayElement(this.apartmentAvailableBlock, true);
    displayElement(this.apartmentNotAvailableBlock, false);

    // populate tarif info
    const currentApartment = apartmentData.rentals[0];

    // map through all tarifs, populate them and return a tarif block element
    const allTarifBlocks = currentApartment.pricing.map((eachPricing) =>
      tarifsInstance.populateTarifs(eachPricing, currentApartment)
    );

    // append all new tarifs in the tarif wrap
    tarifsInstance.tarifWrap.replaceChildren(...allTarifBlocks);

    // select a tarif by default
    const firstTarifRadio = document.querySelector(SELECTORS.tarifRadio) as HTMLInputElement;
    firstTarifRadio.click();
  }
}
