import { URL_PARAMS_KEYS } from './constants';

export const parseUrlParams = () => {
  const searchParams = new URLSearchParams(window.location.search);

  const checkinDate = searchParams.get(URL_PARAMS_KEYS.checkinDate);
  const checkoutDate = searchParams.get(URL_PARAMS_KEYS.checkoutDate);
  const nbAdultsValue = searchParams.get(URL_PARAMS_KEYS.nbAdults);
  const nbChildrenValue = searchParams.get(URL_PARAMS_KEYS.nbChildren);
  const nbBabiesValue = searchParams.get(URL_PARAMS_KEYS.nbBabies);
  const cityId = searchParams.get(URL_PARAMS_KEYS.cityId);
  const cityName = searchParams.get(URL_PARAMS_KEYS.cityName);
  const bookingSyncAptId = searchParams.get(URL_PARAMS_KEYS.bookingSyncAptId);
  if (!nbAdultsValue || !nbChildrenValue || !nbBabiesValue) return undefined;

  const nbAdults = Number(nbAdultsValue);
  const nbChildren = Number(nbChildrenValue);
  const nbBabies = Number(nbBabiesValue);
  return {
    checkinDate,
    checkoutDate,
    nbAdults,
    nbChildren,
    nbBabies,
    cityId,
    cityName,
    bookingSyncAptId,
  };
};
