/* eslint-disable @typescript-eslint/no-explicit-any */
import intlTelInput from 'intl-tel-input';

import type { BookingParameters } from '$types/api';

import { FACEBOOK_KEYS, GA_KEYS, SELECTORS } from './constants';
import { constructUrl } from './constructUrl';
import { convertDateFormat } from './convertDateFormat';
import { displayElement } from './displayElement';
import { getBookingUrl } from './getBookingUrl';
import { getCookieValue } from './getCookieValue';
import { parseUrlParams } from './parseUrlParams';

export class BookingHandler {
  public formModal: HTMLElement;
  public formElement: HTMLElement;
  public bookingLoader: HTMLElement;
  public bookingLoaderText: HTMLElement;
  public searchLoader: HTMLElement;
  public closeBookingElement: HTMLElement;
  public iti: any;
  constructor() {
    this.formModal = document.querySelector(SELECTORS.bookingModal) as HTMLElement;
    this.formElement = document.querySelector(SELECTORS.bookingForm) as HTMLElement;
    this.bookingLoader = document.querySelector(SELECTORS.bookingLoader) as HTMLElement;
    this.bookingLoaderText = document.querySelector(SELECTORS.bookingLoaderText) as HTMLElement;
    this.searchLoader = document.querySelector(SELECTORS.searchLoader) as HTMLElement;

    this.closeBookingElement = document.querySelector(SELECTORS.bookingClose) as HTMLElement;
    this.closeBookingElement.addEventListener('click', () => {
      this.closeBookingModal();
    });

    const countryCodeInput = document.querySelector(SELECTORS.countryCode) as HTMLInputElement;
    this.iti = intlTelInput(countryCodeInput);
  }

  public closeBookingModal() {
    displayElement(this.formModal, false);
  }

  public async handleReserve(e: Event) {
    e.preventDefault();
    e.stopPropagation();
    const bookingSyncId = (
      (e.currentTarget as HTMLAnchorElement) || (e.target as HTMLElement)
    ).getAttribute('bookingsync_id');
    const totalPrice = (
      (e.currentTarget as HTMLAnchorElement) || (e.target as HTMLElement)
    ).getAttribute('price');
    const rateName = (
      (e.currentTarget as HTMLAnchorElement) || (e.target as HTMLElement)
    ).getAttribute('rateName');
    // throw error modal here
    if (!bookingSyncId || !totalPrice || !rateName) return;
    displayElement(this.formModal, true);
    this.formElement.setAttribute('bookingsync_id', bookingSyncId);
    this.formElement.setAttribute('price', totalPrice);
    this.formElement.setAttribute('rateName', rateName);
    this.formElement.addEventListener('submit', async (e: Event) => {
      await this.handleBookingSubmit(e);
    });
  }

  public async handleBookingSubmit(e: Event) {
    e.preventDefault();
    e.stopPropagation();
    const bookingSyncId = (e.currentTarget as HTMLFormElement).getAttribute('bookingsync_id');
    const bookingPrice = (e.currentTarget as HTMLFormElement).getAttribute('price');
    const bookingRateName = (e.currentTarget as HTMLFormElement).getAttribute('rateName');
    const bookingFormData = new FormData(e.currentTarget as HTMLFormElement);
    const bookingFirstName = bookingFormData.get('be-nom');
    const bookingLastName = bookingFormData.get('be-prenom');
    const bookingEmail = bookingFormData.get('be-email');

    const countryCodeData = this.iti.getSelectedCountryData();
    const bookingCountry = countryCodeData.dialCode;
    const bookingPhone = bookingFormData.get('be-phone') as string;
    const bookingPhoneNumber = bookingCountry + bookingPhone;
    const ga_user = getCookieValue(GA_KEYS.gaUser);
    const ga_session = getCookieValue(GA_KEYS.gaSession);
    const fbclid = localStorage.getItem(FACEBOOK_KEYS.fbclid);
    const utmSource = localStorage.getItem(FACEBOOK_KEYS.utmSource);
    const utmMedium = localStorage.getItem(FACEBOOK_KEYS.utmMedium);
    const utmCampaign = localStorage.getItem(FACEBOOK_KEYS.utmCampaign);

    try {
      displayElement(this.bookingLoader, true);
      displayElement(this.searchLoader, true);
      const parsedUrlParams = parseUrlParams();
      if (!parsedUrlParams || !parsedUrlParams.checkinDate || !parsedUrlParams.checkoutDate) {
        throw new Error('Something went wrong');
      }
      const parsedBookingParams: BookingParameters = {
        checkin_date: convertDateFormat(parsedUrlParams.checkinDate),
        checkout_date: convertDateFormat(parsedUrlParams.checkoutDate),
        nb_adults: parsedUrlParams.nbAdults.toString(),
        nb_children: parsedUrlParams.nbChildren.toString(),
        nb_babies: parsedUrlParams.nbBabies.toString(),
        bookingsync_apt_id: bookingSyncId as string,
        price: bookingPrice as string,
        rate_name: bookingRateName as string,
        first_name: bookingFirstName as string,
        last_name: bookingLastName as string,
        email_address: bookingEmail as string,
        phone_number: bookingPhoneNumber,
        ga_user: ga_user as string,
        ga_session: ga_session as string,
        utm_source: utmSource as string,
        utm_medium: utmMedium as string,
        utm_campaign: utmCampaign as string,
        fbclid: fbclid as string,
      };
      const bookingQueryUrl = constructUrl(parsedBookingParams);

      if (!bookingSyncId) {
        throw new Error('Something went wrong');
      }

      const bookingResponse = await getBookingUrl(bookingQueryUrl);

      if (!bookingResponse || bookingResponse.errors) {
        this.bookingLoaderText.textContent = 'Something went wrong. Please try again later.';
        return;
      }

      window.location.replace(bookingResponse.payment_url as string);
    } catch (error: any) {
      this.bookingLoaderText.textContent = error.message as string;
    }
  }
}
