/* eslint-disable prettier/prettier */
import type { CMSList } from '@globalTypes/CMSList';
import Splide from '@splidejs/splide';

import type { CMSAppartmentItem, CMSImage } from '$types/api';

import { ApiClient } from './ApiClient';
import { CATEGORIES, COLLECTIONS, EQUIPMENTS, QUARTIERS, SELECTORS } from './constants';
import { bindData, getApartmentThumbnails } from './utils';

export class Apartments {
  /** The api client */
  private apiClient = new ApiClient();

  /**
   *
   * @param listInstance  The list that the offers will be displayed in
   * @param itemTemplateElement The template element to create new items from
   */
  constructor(
    private listInstance: CMSList,
    private itemTemplateElement: HTMLDivElement
  ) {
    // Remove existing items
    listInstance.clearItems();
    // console.log(listInstance.itemsPerPage);
  }

  /**
   * Fetches the apartments data from the CMS
   */
  public async getApartmentsData() {
    const apartments = await this.apiClient.getCMSApartments();

    if (!apartments) return;

    const listItems: HTMLElement[] = [];
    for (const apartment of apartments.items) {
      //this.createApartmentItem(apartment);
      listItems.push(this.createApartmentItem(apartment));
    }

    this.listInstance.addItems(listItems);
  }

  /**
   * Creates a new apartment item from the template element
   * @param apartment The apartment CMS apartment data
   * @returns The new apartment item
   */
  private createApartmentItem(apartment: CMSAppartmentItem) {
    const newItem = this.itemTemplateElement.cloneNode(true) as HTMLDivElement;
    const { name, neighborhood, category,collection, price, 'number-persons':numberOfPeople, 'number-size':size,
        'number-beds':rooms, 'number-bathrooms':bathrooms, 'equipment-and-services':equipmentsAndServices
     } = apartment.fieldData;

    // apartment thumbnails
    const thumbnails = getApartmentThumbnails(apartment);
    this.setupSliders(thumbnails, newItem);

    // setup the category
    const categories =  CATEGORIES[category]
    if(categories){
        const categoryContainer = newItem.querySelector(SELECTORS.categoryContainer)
        // set the category container class 
        if(categoryContainer){
            categoryContainer.classList.add(`${categories.name.toLowerCase().replace(/ /g, '')}-category`)
            // bind the category name
            bindData(categoryContainer,SELECTORS.category, categories.name)
        }
    }

    // setup the equipments and services
    // const equipments = EQUIPMENTS[equipment];
    if(equipmentsAndServices){
        // bind the equipments and services to a list
        const equipmentsContainer = newItem.querySelector(SELECTORS.equipmentsContainer);
        if(equipmentsContainer){
            equipmentsContainer.innerHTML = '';
            equipmentsAndServices.forEach((equipment) => {
                const equipmentElement = document.createElement('span');
                equipmentElement.setAttribute('fs-cmsfilter-field', 'equipments');
                equipmentElement.innerHTML = EQUIPMENTS[equipment]?.name;
                equipmentsContainer.appendChild(equipmentElement);
            })
        }
    }

    // setup collection
    const collections = COLLECTIONS[collection];
    if (collections) {
      bindData(newItem, SELECTORS.collection, collections.name);
    }


    // bind the apartment name
    bindData(newItem, SELECTORS.name, name);

    // bind number of people
    bindData(newItem, SELECTORS.noOfPeople, numberOfPeople)

    // bind size
    bindData(newItem, SELECTORS.size, size)

    // bind rooms
    bindData(newItem, SELECTORS.rooms, rooms)

    // bind bathrooms
    bindData(newItem, SELECTORS.bathrooms, bathrooms)

    // bind the apartment title
    const quartiers = QUARTIERS[neighborhood];
    if (quartiers) {
      bindData(newItem, SELECTORS.neighborhood, quartiers.name);
    }

     // bind the apartment price
    bindData(newItem, SELECTORS.price, price);

    return newItem
  }

  /**
   * Sets up the sliders for the apartments
   */
  private setupSliders(thumbnails: CMSImage[], newItem: HTMLElement) {
    if (!thumbnails.length) return;
    // remove the arrows
    const sliderArrows = newItem.querySelectorAll('.apartments-slider_arrow');
    sliderArrows.forEach((arrow) => {
      arrow.remove();
    });

    const sliderContainer = newItem.querySelector<HTMLDivElement>(SELECTORS.sliderContainer);
    // sliderContainer?.remove();
    // console.log('Setting up slider----s', newItem);
    if (!sliderContainer) return;
    sliderContainer.classList.add('splide');

    // get the first item in slider as the template
    const firstItem = sliderContainer.querySelector('.apartments-slider_slide');
    if (!firstItem) return;

    sliderContainer.innerHTML = '';

    // create div for the slider arrows
    const arrows = document.createElement('div');
    arrows.classList.add('splide__arrows');

    const prevButton = document.createElement('button');
    prevButton.classList.add('splide__arrow', 'splide__arrow--prev', 'apartments-slider_arrow');
    prevButton.innerHTML = `<span style="width:14px;height:14px;position:relative;right:-1px;top:-2px"><svg width="100%" height="100%" viewBox="0 0 10 18" fill="none" xmlns="http://www.w3.org/2000/svg">
<path d="M8.58251 17.8592C8.4025 17.8592 8.22248 17.7901 8.08466 17.6491L0.388985 9.77317C0.256786 9.63787 0.183655 9.45652 0.183655 9.26365C0.183655 9.07079 0.256786 8.88943 0.388985 8.75414L8.08466 0.87825C8.3603 0.596146 8.80472 0.596146 9.08037 0.87825C9.35602 1.16035 9.35602 1.61518 9.08037 1.8944L1.87974 9.26365L9.07755 16.6329C9.3532 16.915 9.3532 17.3698 9.07755 17.6519C8.93973 17.793 8.75971 17.8621 8.5797 17.8621L8.58251 17.8592Z" fill="CurrentColor"></path>
</svg></span>`;

    const nextButton = document.createElement('button');
    nextButton.classList.add('splide__arrow', 'splide__arrow--next', 'apartments-slider_arrow');
    nextButton.innerHTML = `<span style="width:14px;height:14px;position:relative;right:-1px;top:-2px"><svg width="100%" height="100%" viewBox="0 0 10 18" fill="none" xmlns="http://www.w3.org/2000/svg">
<path d="M0.762064 0.140798C0.94208 0.140798 1.1221 0.209884 1.25992 0.350936L8.95559 8.22683C9.08779 8.36212 9.16092 8.54347 9.16092 8.73634C9.16092 8.92921 9.08779 9.11056 8.95559 9.24586L1.25992 17.1217C0.984269 17.4038 0.539856 17.4038 0.264207 17.1217C-0.0114421 16.8396 -0.0114421 16.3848 0.264207 16.1056L7.46483 8.73634L0.267021 1.36709C-0.00862838 1.08498 -0.00862834 0.630162 0.267021 0.348058C0.404846 0.207006 0.584861 0.13792 0.764877 0.13792L0.762064 0.140798Z" fill="CurrentColor"></path>
</svg></span>`;

    arrows.appendChild(prevButton);
    arrows.appendChild(nextButton);

    sliderContainer.appendChild(arrows);

    // create div for the slider track
    const track = document.createElement('div');
    track.classList.add('splide__track');
    sliderContainer.appendChild(track);

    // create ul for the slider list
    const list = document.createElement('ul');
    list.classList.add('splide__list');

    for (const thumbnail of thumbnails) {
      const listItem = document.createElement('li');
      listItem.classList.add('splide__slide');

      const newItem = firstItem.cloneNode(true) as HTMLDivElement;
      newItem.classList.remove('w-slide');
      const img = newItem.querySelector('img');
      if (img) {
        img.src = thumbnail.url;
        img.alt = thumbnail.alt || '';
        img.srcset = thumbnail.url;
      }

      listItem.appendChild(newItem);
      list.appendChild(listItem);
    }
    track.appendChild(list);

    this.setupSplider(sliderContainer);
  }

  /**
   * Sets up the Splide slider for the apartment.
   */
  private setupSplider(element: HTMLElement) {
    const splide = new Splide(element, {
      type: 'loop',
      pagination: false,
    });

    splide.mount();
  }
}
