import type { CMSAppartmentData, Rental, SearchParameters } from '$types/api';

import { buildQueryParams } from './buildQueryParams';
import { apiBaseUrl } from './constants';

export class ApiClient {
  /**
   *
   * @param queryParams to include as part of query for the request
   * @returns offers data for a group of apartments
   */
  public async getGroupApartmentsData(queryParams: SearchParameters): Promise<Rental[] | null> {
    const url = apiBaseUrl + 'groupApartments' + buildQueryParams(queryParams);

    const response = await fetch(url);
    const body = await response.json();
    if (body.error || !body.data) return null;

    return body as Rental[];
  }

  /**
   *
   * @param queryParams to include as part of query for the request
   * @returns offers data for a single hotel
   */
  public async getSingleHotelOffersData(queryParams: SearchParameters): Promise<Rental | null> {
    const url = apiBaseUrl + 'offers' + buildQueryParams(queryParams);

    const response = await fetch(url);
    const body = await response.json();
    if (body.error || !body.data) return null;

    return body as Rental;
  }

  /**
   *
   * @param queryParams to include as part of query for the request
   * @returns CMS apartment data
   */
  public async getCMSApartments(): Promise<CMSAppartmentData | null> {
    const url = apiBaseUrl + 'cmsApartments';

    const response = await fetch(url);
    const body = await response.json();
    if (!body.items) return null;

    return body as CMSAppartmentData;
  }
}
