// a class to initialize a counter and increment it and decrement it

export class Counter {
  public count: number;
  public countElement: HTMLElement;
  constructor(
    countElement: HTMLElement,
    incrementSelector: string,
    decrementSelector: string,
    initialCount: number = 0,
    totalCountElement: HTMLInputElement,
    totalCountChangeEvent?: Event
  ) {
    this.count = initialCount;
    totalCountElement.value = (Number(totalCountElement.value) + initialCount).toString();
    this.countElement = countElement;
    const incrementElement = document.querySelector(incrementSelector) as HTMLElement;
    const decrementElement = document.querySelector(decrementSelector) as HTMLElement;

    incrementElement.addEventListener('click', () => {
      this.increment(totalCountElement);
      if (totalCountChangeEvent) {
        totalCountElement.dispatchEvent(totalCountChangeEvent);
      }
      this.countElement.textContent = this.getCount().toString();
    });

    decrementElement.addEventListener('click', () => {
      this.decrement(totalCountElement);
      if (totalCountChangeEvent) {
        totalCountElement.dispatchEvent(totalCountChangeEvent);
      }
      this.countElement.textContent = this.getCount().toString();
    });

    this.countElement.textContent = this.getCount().toString();
  }

  public increment(totalCountElement: HTMLInputElement): void {
    this.count++;
    totalCountElement.value = (Number(totalCountElement.value) + 1).toString();
  }

  public decrement(totalCountElement: HTMLInputElement): void {
    if (this.count === 0) return;
    this.count--;

    totalCountElement.value = (Number(totalCountElement.value) - 1).toString();
  }

  public getCount(): number {
    return this.count;
  }

  public setCount(count: number): void {
    this.count = count;

    this.countElement.textContent = this.getCount().toString();
  }
}
