import { Apartments } from '$utils/Apartments';
import { BookingHandler } from '$utils/BookingHandler';
import { SELECTORS } from '$utils/constants';
import { initDatePicker } from '$utils/initDatePicker';
import { SingleApartmentSearchBar } from '$utils/SingleApartmentSearchBar';
import { singleApartmentTarifReserveHandler } from '$utils/singleApartmentTarifReserveHandler';
import { Tarifs } from '$utils/Tarifs';

window.Webflow ||= [];
window.Webflow.push(async () => {
  const datePicker = initDatePicker();

  // initialing Search Bar insatnce
  const singleApartmentSearchBarInstance = new SingleApartmentSearchBar();
  const apartmentsInstance = new Apartments();
  const tarifsInstance = new Tarifs();
  await singleApartmentSearchBarInstance.handleSearch(apartmentsInstance, tarifsInstance);
  // getting query url from search bar instance

  // initializing booking handler
  const bookingHandlerInstance = new BookingHandler();

  // Handdling search bar changes

  // on changing travellers info
  singleApartmentSearchBarInstance.totalCountElement.addEventListener(`change`, async () => {
    await singleApartmentSearchBarInstance.handleSearch(apartmentsInstance, tarifsInstance);
  });

  // on changing travel dates info
  datePicker.on('select', async () => {
    await singleApartmentSearchBarInstance.handleSearch(apartmentsInstance, tarifsInstance);
  });

  // Handle tarif form submission (i.e when user clicks on reserve button with a selecte tarif)

  const apartmentReserveForm = document.querySelector(
    SELECTORS.apartmentReserveForm
  ) as HTMLFormElement;
  apartmentReserveForm.addEventListener('submit', async (e: Event) => {
    e.preventDefault();
    e.stopPropagation();

    singleApartmentTarifReserveHandler(
      e,
      singleApartmentSearchBarInstance.bookingSyncAptId,
      bookingHandlerInstance
    );
  });
});
